<template>
  <div class="CourseVideo">
    <el-row>
      <el-col :span="19">
        <el-row class="slide">
          <p class="SlideTitle"> 第一章  课件一
            <router-link to="/CourseWare/1/1"><button>在线課件</button></router-link>
          </p>
          <div class="Video">
            <my-video :sources="video.sources" :options="video.options"></my-video>
          </div>
        </el-row>
      </el-col>
      <el-col :span="5">
        <el-tabs value="教学大纲">
          <el-tab-pane label="教学大纲" name="教学大纲">
            <el-button class="introduce"  type="primary" @click="dialogVisible = true">本章概要</el-button>
            <el-menu default-active="1" v-for="item in sections" class="el-menu-vertical-demo"  :router="true" :unique-opened="true">
              <el-submenu :index="item.id" :key="item.id">
                <template slot="title" :index="item.id"><span>{{item.tag}}</span></template>
                <el-submenu :index="items.id" v-for="(items,index) in item.chapters"  class="submenu">
                  <template slot="title">{{items.tag}}{{items.title}}</template>
                  <el-menu-item   :index="/CourseVideo/+item.id+'/'+items.id" class="menuItem" @click="selectSection($route.params.section)" v-for="itemss in items.list" @click.native="selectVideoSrc(itemss.src)">{{itemss.filename}}</el-menu-item>
                </el-submenu>
              </el-submenu>
            </el-menu>
          </el-tab-pane>
          <el-tab-pane label="教学要点" name="教学要点">

          </el-tab-pane>
        </el-tabs>
      </el-col>
    </el-row>
    <el-dialog
      title="本章概要"
      :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
      <span  class="slide"> <pdf :src="pdf.src" :page="pdf.page"></pdf>
      <div class="left">
        <img src="../../../static/images/left.png" @click="prev()">
      </div>
      <div class="right">
        <img src="../../../static/images/right.png" @click="next()">
      </div>
      </span>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
  </span>
    </el-dialog>
  </div>
</template>
<script>
  import Router from 'vue-router'
  import Vue from 'vue';
  import myVideo from 'vue-video';
  import data from '../../../static/data.json';
  import axios from 'axios';
  import qs from 'qs';
  import pdf from 'vue-pdf';
  export default {
    components: {
      myVideo,pdf
    },
    name: 'Top',
    data () {
      return {
        dialogVisible: false,
        pdf:{
          page:1,
          src:"../../static/33.pdf"
        },
        sections:"",
        video: {
          sources: [{
            src: '../../../static/video/hah.mp4',
            type: 'video/mp4'
          }],
          options: {
            autoplay:false,
            volume: 0.6,
            poster: '../../../static/images/timg.gif'
          }
        },
        src: "../static/images/1.jpg", selectItem: 0,
        courseData:[]
      }
    },
    mounted: function () {
      this.sections = data;
      this.getSectionAll();
    },
    methods: {
      selectSection(value)
      {
        this.Images=[];
        this.sectionImages.forEach((e)=>{
          if(e.id==value)
          {
            this.Images=e.Images;
            console.log(e.id);
          }
        });
      },
      prev(){
        if(this.pdf.page>0) {
          this.pdf.page--;
        }

      },
      next(){
        this.pdf.page++;

      },
      showImage(value)
      {
        this.selectItem = value;
        this.src = this.Images[value].ImageSrc;
        for (let i = 0; i < this.Images.length; i++) {
          this.Images[i].isActive = false;
        }

      },
      showIsActive(){
        for (let i = 0; i < this.Images.length; i++) {
          this.Images[i].isActive = false;
        }
      },
      //获取章节的信息
      getSectionAll()
      {
        this.courseData=[];
        axios.post('http://wheatflow.club/php/qnbasemam.php',
          qs.stringify({
            act: "selectSrc",
            type: "CourseVideoManager",
          }))
          .then(response => {
              console.log(response.data);
            let k = 0;
            for (let i = 0; i < response.data.length; i++) {
              Vue.set(this.courseData, k, response.data[i]);
              k++;
            }
            console.log(this.courseData);
            this.pushSection();
          })
          .catch(error => {
            console.log(error);
            alert("网络错误");
          });
      },
      //课程信息导入到章节信息表
      pushSection()
      {
        let arr=[];
        let k=0;
        let m=0;
//        for (let i = 0; i < this.section[0].chapters.length; i++)
//        {
//          this.section[0].chapters[i].list=null;
//        }
        for (let j = 0; j < this.courseData.length; j++) {
            for (let i = 0; i < this.sections.length; i++) {
              if (this.sections[i].id == this.courseData[j].章节) {
                for (let h = 0; h < this.sections[i].chapters.length; h++) {
                  if (this.sections[i].chapters[h].id == this.courseData[j].节数) {
                    k = this.courseData[j].节数 - 1;
                    arr[m] = {
                      id: this.courseData[j].ID,
                      sort: this.courseData[j].顺序,
                      src: this.courseData[j].地址,
                      filename: this.courseData[j].文件名
                    };
                    m++;
                  }
                }
                Vue.set(this.sections[i].chapters[k],'list',arr);
              }
          }
        }
        console.log(this.sections);
      },
      selectSection(index,path)
      {
        let arr = {};
        this.section = [];
        this.sections.forEach((e) => {
          if (e.id == index) {
            arr = {tag: e.tag, title: e.title, chapters: e.chapters,id: e.id};
            this.section.push(arr);
          }
        });
      },
      selectVideoSrc(value)
      {
         Vue.set(this.video.sources[0],'src',value);
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .CourseVideo {
    width: 100%;
    height: 500px;
    margin: 50px auto;
  }

  .slide {
    background-color: #313131;
  }

  .slide:hover .SlideTitle button {
    display: block;
  }

  .slide:hover .SlideTitle {
    display: block;
  }

  .Video {
    background-color: #d2d2d2;
    position: relative;
    margin: 80px 20px;
    margin-bottom: 20px;
    height: auto;
    max-height: 800px;
  }

  .SlideTitle {
    height: 60px;
    background-color: rgba(0, 0, 0, 0.5);
    font-family: "Microsoft YaHei UI";
    font-size: 20px;
    color: #ffffff;
    position: absolute;
    text-align: left;
    top: 0;
    width: 100%;
    margin: 0 auto;
    line-height: 60px;
    display: none;
  }

  .SlideTitle button {
    background-color: #fd7e26;
    border: none;
    float: right;
    height: 100%;
    color: #ffffff;
    display: none;
    text-align: center;
  }

  .SlideImage {
    margin: 80px 50px;
  }

  .SlideImage img {
    width: 100%;
    height: 600px;
  }

  .show {
    position: absolute;
    visibility: hidden;
  }

  .menuList {
    background-color: #ffffff;
    margin-top: 90px;
  }

  .submenu {
    font-family: "Microsoft YaHei UI";
    font-size: 24px;
    color: rgba(0, 0, 0, 0.8);
    text-align: left;
    text-indent: 10px;
    border-bottom: 1px solid #e5e5e5;
  }

  .menuItem {
    color: rgba(0, 0, 0, 0.5);
  }
  .introduce{
    margin: 20px auto;
  }
  .slide .left {
    width: 60px;
    height: 180px;
    position: absolute;
    left: 0;
    line-height: 180px;
    top:300px;
  }

  .slide .left img {
    width: 60px;
    height: 60px;
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
  }

  .slide .right {
    width: 60px;
    height: 180px;
    position: absolute;
    right: 0;
    line-height: 180px;
    z-index: 1;
    top:300px;
  }

  .slide .right img {
    width: 60px;
    height: 60px;
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
  }

  .slide:hover .left {
    display: inline-block;
    background-color: #f6f6f6;
  }

  .slide:hover .right {
    display: inline-block;
    background-color: rgba(210, 210, 210, 0.8);
    z-index: 2;
  }

  .slide {
    background-color: #e5e5e5;
  }


</style>



