import Vue from 'vue'
import Router from 'vue-router'
import CourseWare from '@/components/page/CourseWare'
import CourseVideo from '@/components/page/CourseVideo'
import index from '@/components/page/index'
Vue.use(Router)

export default new Router({
  routes: [
   {
      path:'/',
      name: 'index',
      component: index
    },{
      path:'/CourseWare/:section/:chapter',
      component: CourseWare,
      name:CourseWare
    }, {
      path:'/CourseWare/:section/:chapter',
      component: CourseWare,
      name:CourseWare,
      redirect:'/CourseWare/:section/:chapter'
    },{
      path:'/CourseVideo/:section/:chapter',
      component: CourseVideo,
      name:CourseVideo
    },{
      path:'/CourseVideo/:section/:chapter',
      component: CourseVideo,
      redirect:'/CourseVideo/:section/:chapter'
    },
  ]
})



