<template>
  <div class="TeacherNav">
    <div class="center">
      <h2>负责团队</h2>
    <el-row class="clearfix">
      <!--<el-col :span="10">-->
        <!--<div v-for="item in teachernames" class="leftbox">-->
          <!--<div class="smallbox">-->
            <!--<strong>{{item.name}}</strong><br>-->
            <!--<span>{{item.professionaltitles}}</span>-->
          <!--</div>-->
        <!--</div>-->
      <!--</el-col>-->
      <!--<el-col :span="4">-->
        <div class="mainbox" @click="open()" >
          <div class="smallbox">
            <strong>孟宪军</strong><br>
            <span>负责人</span>
          </div>
        </div>
      <!--</el-col>-->
      <!--<el-col :span="10">-->
        <!--&lt;!&ndash;<div v-for="item in teachernames" class="rightbox">&ndash;&gt;-->
          <!--<div class="smallbox">-->
            <!--<strong>{{item.name}}</strong><br>-->
            <!--<span>{{item.professionaltitles}}</span>-->
          <!--</div>-->
        <!--</div>-->
      <!--</el-col>-->
    </el-row>
    </div>
    <small><div class="margin">所属学校  广州中医药大学</div>
      <div class="margin">
        <div class="marginright"> 负责人</div>  张宏</div>
      <div class="margin">
        <div class="marginright">课程类型</div>  测实结合</div>
      <div class="margin">
        <div class="marginright">课程性质</div>  专业课</div>
      <div class="margin">
        <div class="marginright">学科门类</div>  医学类</div>
      <div class="margin">
        <div class="marginright">结合专业</div>  针灸，中医，康复等</div></small>
    <el-popover ref="popover4" placement="right" width="400" trigger="click" disabled="true"></el-popover>
  </div>
</template>

<script>
  import ElCol from "element-ui/packages/col/src/col";
  export default {
    components: {ElCol},
    name: 'Body',
    data () {
      return {
          teachernames:
              [
                { name: '张宪军',
                  professionaltitles:'中医副教授'
                },
              ],
        introduce:"教师介绍教师介绍教师介绍教师介绍教师介绍教师介绍教师介绍教师介绍教师介绍教师介绍教师介绍教师介绍"

      }
    },
    mounted:function () {
     var img ={src: '../static/images/2.jpg'};
      this.getImgNaturalFimensions(img);
      let NauutalFimensions={};
      NauutalFimensions=this.getImgNaturalFimensions(img);
      let Divwidth=document.body.clientWidth;
      let DivHeight=parseInt((NauutalFimensions.nHeight/NauutalFimensions.nWidth)*Divwidth);
      this.setClassSize('TeacherNav',DivHeight);
    },
    methods: {
      open() {
        this.$alert(this.introduce, '教师介绍', {
          confirmButtonText: '确定',
        });
      },
      getImgNaturalFimensions(img)
      {
          let NauutalFimensions={};
          var image=new Image();
          image.src=img.src;
        NauutalFimensions=this.onload(image);
        return NauutalFimensions;
      },
      onload(image)
      {
          let nWidth=0,nHeight=0;
          nWidth=image.width;
          nHeight=image.height;
          return {nWidth,nHeight};
      },
      setClassSize(classname,height)
      {
        let classnames=document.getElementsByClassName(classname);
        classnames[0].style.height=height+'px';
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .clearfix:before,.clearfix:after{
    display: table;
    content: "";
  }
  .clearfix:after{
    clear: both;
  }
  .clearfix{
    *zoom:1;
  }
  .TeacherNav{
    background-image: url("../../../static/images/2.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    width:100%;
    margin: 0 auto;
    position: relative;
    min-height: 550px;
  }
  .TeacherNav .center h2{
    color: #ffffff;
    font-size: 45px;
    font-family: "Microsoft YaHei UI Light";
    text-align: left;
    width: 550px;
    margin: 0 auto;
    margin-bottom: 35px;
   padding-top: 50px;
  }
  .mainbox{
    background-color: #eeeeee;
    height: 250px;
    margin: 0 auto;
    width: 250px;
    position: relative;
  }
  .leftbox{
    float: right;
    background-color: #eeeeee;
    height: 130px;
    margin: auto 20px;
    width: 130px;
    position: relative;
  }
  .rightbox{
    float: left;
    background-color: #eeeeee;
    margin: auto 20px;
    width: 130px;
    height:130px;
    position: relative;
  }
  .smallbox{
    background-color: #ffffff;
    font-family: "Microsoft YaHei UI Light";
    height: 50px;
    padding-top: 10px;
    width: 100px;
    position: absolute;
    bottom: -20px;
    margin: auto;
    left:0;
    right:0;
    text-align: center;
  }
  .smallbox strong{
    font-size: 18px;
    padding-top: 20px;
    margin-top: 10px;
    line-height: 1.5;
  }
  .smallbox span{
    font-size: 12px;
  }
  .TeacherNav small{
    font-family: "Microsoft YaHei UI Light";
    font-size: 12px;
    color: rgba(255,255,255,0.5);
    position: absolute;
    bottom: 10px;
    left: 0;
    right: 0;
  }
.margin{
  margin-right: 30px;
  display: inline-block;
}
  .marginright{
    margin-right: 10px;
    display: inline-block;
  }

</style>



