<template>
  <div class="Head">
      <div class="HeadBackground" id="headbackground">
        <img src="../../../static/images/1.jpg" style="display: none">
        <div class="right">
          <img src="../../../static/images/logo.png">
          <h1>针灸学</h1>
          <p>国家级精品资源共享课程《针灸学》 依托天津中医药大学国家级重点学科
            ，教学示范中心及国内最大的针灸临床教学基地，由石学敏院士衔领国家
            级教学团队系统讲授，课程包括经络腧穴学，刺灸法及针灸临床应用，体现
            了多元化的教学方法，展现了针灸名家的学术思想和临床经验</p>
          <router-link to="/CourseVideo/1/1"><button type="text">开始学习</button></router-link>
        </div>
       </div>
  </div>
</template>
<script>
  export default {
    name: 'Top',
    data () {
      return {
      }
    },
    mounted:function () {
        var img =document.getElementsByTagName('img')[0];
      let NauutalFimensions={};
      NauutalFimensions=this.getImgNaturalFimensions(img);
      let Divwidth=document.body.clientWidth;
      let DivHeight=parseInt((NauutalFimensions.nHeight/NauutalFimensions.nWidth)*Divwidth);
      this.setClassSize('HeadBackground',DivHeight);
    },
    methods: {
        getImgNaturalFimensions(img,callback)
        {
            let nWidth=0,nHeight=0;
            if(img.naturalWidth)
            {
                nWidth=img.naturalWidth;
                nHeight=img.naturalHeight;
                return{nWidth,nHeight};
            }
            else {
              let NauutalFimensions={};
                var image=new Image();
                image.src=img.src;
              NauutalFimensions=this.onload(image);
              return NauutalFimensions;
        }
        },
      onload(image)
      {
        let nWidth=0,nHeight=0;
        nWidth=image.width;
        nHeight=image.height;
        return {nWidth,nHeight};
      },
        setClassSize(classname,height)
        {
            let classnames=document.getElementsByClassName(classname);
            classnames[0].style.height=height+'px';
        }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .Head{
    width: 100%;
    margin-bottom: 30px;
  }
  .HeadBackground{
    background-image: url("../../../static/images/1.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    width:100%;
    min-height: 510px;
  }
  .HeadBackground img{
    width:100%;
  }
  .clearfix:before,.clearfix:after{
    display: table;
    content: "";
  }
  .clearfix:after{
    clear: both;
  }
  .clearfix{
    *zoom:1;
  }
  .right{
    float: right;
    width: 30%;
    margin-right:50px;
    margin-top: 20px;
    height: 90%;
  }
  .right img{
    width: 130px;
  }
  .right h1{
    font-size: 60px;
    font-family:"Microsoft YaHei UI-Bold";
    color:#ffffff;
    opacity: 1;
    margin:10px 0 0 0;
  }
  .right p{
    font-size: 14px;
    font-family:"Microsoft YaHei UI";
    color:#ffffff;
    line-height: 30px;
    text-align: left;
    margin:20px 0 0 0;
  }
  .right button{
    font-size: 24px;
    font-family:"MicrosoftYaHeiUI";
    color:#ffffff;
    text-align: center;
    margin:20px 0 0 0;
    padding: 10px;
    width: 100%;
    border: solid 2px #ffffff;
    background-color:transparent;
  }
  .right button:hover{
    background-color: #ffffff;
    color: rgba(0,0,0,0.8);
    background-style: none;

  }


</style>



