<template>
  <div id="index">
    <HeadNav></HeadNav>
    <TeacherNav></TeacherNav>
    <CourseNav></CourseNav>

      <el-tag type="info" v-for="teachPoint in teachPoints"  disable-transitions="true">{{teachPoint}}</el-tag>
      <!--<ul>-->
        <!--<li :key="teachPoint"  v-for="teachPoint in teachPoints">{{teachPoint}}</li>-->
      <!--</ul>-->
      <!--<el-tabs>-->
        <!--<el-tab-pane :label="teachPoint"  :key="teachPoint"   v-for="teachPoint in teachPoints">-->
          <!--&lt;!&ndash;<div class="CourseIntroduction"><p>{{teachPoint}}</p></div>&ndash;&gt;-->
        <!--</el-tab-pane>-->
      <!--</el-tabs>-->
  </div>
</template>

<script>
  import HeadNav from '@/components/common/HeadNav'
  import TeacherNav from '@/components/common/TeacherNav'
  import CourseNav from '@/components/common/CourseNav'
  export default {
    name: 'index',
    components :{
      HeadNav,TeacherNav,CourseNav
    },
    data(){
        return{
          teachPoints: ['经络系统', '腧穴主治特点', '腧穴定位方法', '经脉循行', '刺针起源于发展', '进针法', '行针手法', '补泻手法', '拔罐法'],

        }
    }
  }
</script>

<style>
  #index {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
  .teachPoint{
    width: 100%;
    font-family:"MicrosoftYaHei";
    font-size: 16px;
    color: rgba(0,0,0,0.8);
    background-color: #eeeeee;
  }
  .teachPoint p{
    text-align: center;
    color:rgba(255,255,255,0.5);
    font-size: 20px;
    padding-top: 5px;
  }
  .teachPoint ul{
    width: 960px;
    margin: 0 auto;
  }
  .teachPoint ul li{
    list-style: none;
    display: inline-block;
    padding-top: 2px;
    margin-right: 10px;
  }
  .CourseIntroduction{
    width: 100%;
    margin: 0 auto;
    width: 960px;

  }
  .CourseIntroduction p{
    font-size: 14px;
    color: rgba(0,0,0,0.8);
    line-height: 20px;
    margin-top:20px ;
    margin-bottom: 30px;
    text-align: left;
  }
</style>



