          Vue.set(this.pdf,'numPages',numPage);
      },
      handleProgress(value)
      {
        Vue.set(this.pdf,'progress',value*100);
        if(value==1)
        {
            this.title="加载完成";
          this.dialogVisible=false;
        }
      },
      handlePageLoaded(loaded)
